.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    
    padding-top: 70px;
    width: 90%;
}

.movies .movie {
display: flex;
width: 45%;
background-color: white;
align-items: flex-start;
margin-bottom: 70px;
justify-content: space-between;
font-weight: 300;
padding: 20px;
border-radius: 5px;
color: #adaeb9;
box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0025);
}

.movie img{
    position: relative;
    max-width: 220px;
    width: 100%;
    top: -30px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0025);
}

.movie .movie_title,
.movie .movie_year {
    font-weight: 300;
    margin: 0;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres {
    list-style: none;
    padding: 0;
    margin: 5px 0;
    display: flex;
    flex-wrap: wrap;
}

.movie_genres li,
.movie .movie_year {
    font-size: 18px;
    margin-right: 10px;
}

.movie .movie_column {
    width: 80%;
}

.movie_summary {
    font-size: 24px;
}