* {
  box-sizing: border-box;
}

body {
  background-color: #eff3f7;
  margin: 0;
  padding: 0;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

.navigation {
  margin: auto auto;
  padding: 50px;
  width: 100%;
  max-width: 1200px;
}

h1 {
  font-family: 'Merriweather', serif;
  text-align: center;
}

.top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}




